@charset "UTF-8";
.no-scroll {
  touch-action: none; }

.theme {
  --color-cell-border: hsla(0, 0%, 0%, 0.1);
  --color-cell-border-dark: hsla(0, 0%, 0%, 0.25);
  --color-cell-hover: hsla(173, 36%, 95%, 1);
  --color-cell-hover-text: hsla(173, 40%, 51%, 1);
  --color-drag-box-border: hsla(209, 97%, 50%, 0.5);
  --color-drag-box-background: hsla(209, 97%, 65%, 0.5);
  --color-handle-wrapper-text: hsla(209, 100%, 80%, 0.5);
  --color-timeline-text: hsl(0, 0%, 48%);
  --color-header-background: white;
  --color-event-text: white;
  --color-event-background: hsl(173, 40%, 51%);
  --color-event-active-background: hsl(173, 40%, 51%);
  --color-event-hover-background: hsl(173, 40%, 51%);
  --color-event-shadow-1: hsla(202, 100%, 16%, 0.5);
  --color-event-shadow-2: rgba(217, 217, 217, 0.5);
  --color-header-shadow-1: hsla(0, 0%, 50%, 0.2);
  --color-header-shadow-2: hsla(0, 0%, 85%, 0.239);
  --color-timeline-background: white;
  --color-grid-background: white;
  --color-header-text-past: hsla(0, 0%, 0%, 0.25);
  --color-header-text-current: hsl(212, 85%, 44%); }

.root {
  --z-index-handle-wrapper: 1;
  --z-index-layer-container: 1;
  --z-index-event: 2;
  --z-index-event-is-pending-creation: 1;
  --z-index-event-active: 3;
  --z-index-draggable: 2;
  --z-index-dragging: 3;
  --z-index-day-header-row: 2;
  --z-index-header: 1;
  --z-index-timeline: 2;
  --z-index-timeline-header: 1;
  contain: strict;
  overflow: auto;
  margin-top: 24px;
  background: var(--color-grid-background, white);
  border: 1px solid var(--color-cell-border);
  border-top: none; }
  .root,
  .root * {
    box-sizing: border-box; }
  .root ::-webkit-scrollbar {
    width: 0px;
    background: transparent; }

.grid-root {
  position: relative;
  display: table;
  overflow: auto;
  width: 100%; }

.debug:not(.debug-active) {
  display: none; }

.calendar {
  user-select: none;
  text-align: center;
  display: flex;
  position: relative;
  border-collapse: collapse; }

.react-draggable {
  position: absolute; }

.handle-wrapper {
  z-index: var(--z-index-handle-wrapper);
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background-position: center;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-size: auto 100%;
  box-sizing: border-box;
  color: var(--color-handle-wrapper-text);
  vertical-align: middle; }
  .handle-wrapper .handle.top {
    top: -6px !important; }
  .handle-wrapper .handle.bottom {
    bottom: -4px !important; }
  .handle-wrapper .handle.top::after,
  .handle-wrapper .handle.bottom::after {
    display: block;
    content: '';
    background: white;
    width: 12px;
    height: 12px;
    border-radius: 12px;
    border: 2px solid #093971;
    margin: 0 auto;
    box-sizing: border-box; }

.layer-container {
  position: relative;
  height: 100%;
  z-index: var(--z-index-layer-container); }
  .layer-container .calendar {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  .layer-container .event {
    z-index: var(--z-index-event); }
  .layer-container .calendar {
    position: absolute;
    z-index: var(--z-index-calendar); }
  .layer-container .drag-box {
    will-change: transform;
    contain: strict;
    position: absolute;
    overflow: hidden;
    text-overflow: ellipsis;
    left: 0;
    top: 0;
    z-index: var(--z-index-drag-box);
    border: 3px dashed var(--color-drag-box-border);
    background: var(--color-drag-box-background);
    width: 100px;
    height: 100px; }

.draggable {
  z-index: var(--z-index-draggable); }

.button-reset {
  outline: none;
  appearance: none;
  background: none;
  border: none;
  border-radius: 0;
  all: unset;
  text-align: initial;
  color: inherit; }

.is-draggable {
  user-select: none;
  cursor: grab; }

.is-draggable:global(.react-draggable-dragging) {
  cursor: grabbing;
  z-index: var(--z-index-dragging) !important; }

.tooltip {
  padding: 0; }
  .tooltip button {
    padding: 0.3125rem 0.5625rem;
    appearance: none;
    background: transparent;
    border: 0;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer; }
    .tooltip button .icon {
      filter: brightness(0) invert(1); }

.event {
  will-change: height;
  user-select: none;
  display: block;
  z-index: var(--z-index-event);
  margin: 0;
  position: absolute;
  font-size: 1rem;
  outline: none;
  background: var(--background, var(--color-event-background));
  color: var(--color-event-text);
  left: 0;
  right: 0;
  width: 100px;
  height: 50px;
  text-overflow: ellipsis;
  border: 1px solid #32a093;
  box-sizing: border-box;
  box-shadow: 0px 2px 5px rgba(50, 160, 147, 0.5);
  border-radius: 8px; }
  .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):focus, .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):hover {
    box-shadow: 0 1px 5px 1px var(--color-event-shadow-1), 0 5px 10px 1px var(--color-event-shadow-2); }
  .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):focus-within {
    box-shadow: 0 1px 5px 1px var(--color-event-shadow-1), 0 5px 10px 1px var(--color-event-shadow-2); }
  .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):hover {
    --background: var(--color-event-hover-background); }
  .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):focus {
    --background: var(--color-event-active-background);
    z-index: var(--z-index-event-active); }
  .event:not(.is-pending-creation):not([disabled]):not(.is-disabled):focus-within {
    --background: var(--color-event-active-background);
    z-index: var(--z-index-event-active); }
  .event.is-pending-creation {
    z-index: var(--z-index-event-is-pending-creation);
    opacity: 0.75; }

.hours-container {
  display: flex; }

.day-column {
  flex: 1 1 var(--cell-width, 250px);
  min-width: var(--cell-min-width, 150px); }
  .day-column:first-child .day-hours {
    border-left: 1px solid var(--color-cell-border); }
  .day-column .day-hours {
    display: flex;
    flex-direction: column;
    position: relative; }
    .day-column .day-hours .cell:hover {
      background: var(--color-cell-hover);
      border: 2px dashed var(--color-cell-hover-text);
      border-radius: 8px;
      cursor: pointer; }
      .day-column .day-hours .cell:hover:after {
        content: '+ Add availability';
        color: var(--color-cell-hover-text);
        margin: 6px;
        font-weight: 600;
        font-size: 12px; }
  .day-column .cell {
    flex: 1 1 var(--cell-height, 40px);
    display: flex;
    justify-content: flex-start;
    align-items: flex-start; }
    .day-column .cell .time {
      color: var(--color-timeline-text);
      font-size: 0.75em;
      text-transform: uppercase; }
  .day-column .title {
    padding: 10px 10px; }

.cell:first-child,
.cell + .cell {
  border: 1px solid var(--color-cell-border);
  border-bottom: 0;
  border-left: 0; }

.cell:not(.is-hour-start) {
  border-top-width: 1px;
  border-top-style: dashed; }

.header .cell {
  border-top-width: 0; }
  .header .cell.is-passed {
    color: var(--color-header-text-past); }
  .header .cell.is-current {
    color: var(--color-header-text-current); }

.header .cell .day-header-row {
  z-index: var(--z-index-day-header-row); }

.sticky-top {
  position: sticky;
  top: 0;
  border-bottom: 1px solid var(--color-cell-border-dark);
  z-index: var(--z-index-day-header-row); }

.sticky-left {
  position: sticky;
  left: 0; }

.header {
  will-change: transform;
  z-index: var(--z-index-header);
  background: var(--color-header-background); }

.day-column.first {
  width: 100px;
  max-width: 100px;
  min-width: 0;
  flex-grow: 0;
  flex-shrink: 0; }

.popup {
  position: absolute;
  top: 0;
  left: 0; }

.range-boxes .event-content {
  padding: 8px 10px;
  font-weight: bold;
  font-size: 13px;
  width: 100%;
  height: 100%;
  white-space: pre-wrap; }
  .range-boxes .event-content > * {
    width: 100%; }

.range-boxes .start {
  display: inline; }
  .range-boxes .start:after {
    content: ' – ';
    display: inline; }

.range-boxes .end {
  display: inline; }

.range-boxes .status {
  display: inline-block;
  font-weight: 400;
  font-size: 12px; }

.timeline {
  display: table-cell;
  text-align: end;
  z-index: var(--z-index-timeline); }
  .timeline .day-column {
    background: var(--color-timeline-background);
    min-width: auto;
    flex: 0 0 auto; }

.timeline .header {
  z-index: var(--z-index-timeline-header);
  box-shadow: none;
  background: var(--color-timeline-background); }
  .timeline .header .cell {
    visibility: hidden; }

.timeline > *:not(.header) .cell {
  position: relative;
  top: -8px;
  border: none;
  border-color: transparent;
  text-align: end;
  justify-content: flex-end; }
  .timeline > *:not(.header) .cell:first-child {
    visibility: hidden; }
  .timeline > *:not(.header) .cell:hover,
  .timeline > *:not(.header) .cell + .cell:hover {
    background: var(--color-grid-background);
    border: none; }
    .timeline > *:not(.header) .cell:hover:after,
    .timeline > *:not(.header) .cell + .cell:hover:after {
      content: none; }
  .timeline > *:not(.header) .cell .time {
    background: inherit;
    padding-right: 1rem; }
